// Kart√≥dromo 1.200 metros
export class Pista {
    
    constructor(codigo, comprimento) {
        this._codigo = codigo;
        this._comprimento = comprimento;

        Object.freeze(this);
    }
    
    get codigo() {
        return this._codigo;
    }
    
    get comprimento() {
        return this._comprimento;
    }
}







